<template>
    <div class="column">
        <Headerl></Headerl>
        <div class="shop" v-for='(item,index) of this.$store.state.column' :key='item._id'>
            <img :src='item.src'/>
            <span>{{Math.round(item.price)*$store.state.count}}</span>
            <div class="btn">
                <button @click='add(0)' class="btn1">+</button>
                    {{$store.state.count}}
                <button @click='rec(0)' class="btn2">-</button>
            </div>
        </div>
    </div>
</template>

<style scoped>
    .column .shop{width: 100%;border-bottom: .1rem solid #d6dada;}
    .column .shop img{width: 2.5rem;height: 2.5rem;margin:.2rem .2rem;}
    .column .shop span{color: #000;font-size: .5rem;display: inline-block;margin:2.2rem 2.5rem 0 0rem;float: right;}
    .column .shop .btn{margin:0rem 0rem 0 .75rem;}
</style>


<script>

import Headerl from '../components/Headerl'
import List from '../components/List'

export default {
    name:'column',
    data(){
        return{
            column:{}
        }
    },
    components:{Headerl,List},
    mounted(){
        this.$store.dispatch({type:'UPDATE_COLUMN'})
    },
    methods:{
        add(){
            this.$store.dispatch('add')
        },
        rec(){
            this.$store.dispatch('rec')
        }
    }
}    
</script>

