<template>
    <div class="user">
        <Headerm></Headerm>
    <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
    </ul>
    </div>
</template>

<script>

import Headerm from '../components/Headerm'

import router from '../plugins/router'

export default {
      data(){
            return {
            user:{}
            }
        },
  components:{Headerm},
  beforeRouteEnter(to,from,next){
    // console.log('/user.....')
    //
    axios({
      url:'/api/user'
    }).then(
      res=>{
        if(res.data.err===0){
          next(_this=>_this.user = res.data.data)
        }else{
          // this.$router.push('/login') 
          // router.push('/login')
          next('/login')
        }
      }
    )
  },
  methods:{
    logout(){
      axios({
        url:'/api/logout',
        method:'delete'
      }).then(
        res=>res.data.err === 0 && this.$router.push('/login')
      )
    }
  }
}

    

</script>

<style scoped>
    .user{}
    .user ul{width: 100%;display: flex;justify-content:space-around;}
    .user ul li{font: .35rem/.8rem '';}
    .user ul li:hover{border-bottom:.1rem solid #3285ff;}


</style>

