<template>
    <div class="details">
        <Headerx></Headerx>
        <div class="xq">
            <img :src='details.src'/>
            <!-- <img :src='details.srcs'/> -->
            <p class="p1">{{details.price}}</p>
            <p class="p2">{{details.name}}</p>
            <div class="fg"></div>
            <h3></h3>
            <ul>
                <li v-for='(item,index) of detail' :key='item._id'>
                    <img :src='item.src'/>
                    <p class="p3">{{item.name}}</p>
                    <p class="p4">{{item.price}}</p>
                </li>
            </ul>
            <p class="p5">-G-  </p>
        </div>
        
    </div>
</template>

<style scoped>
    .xq{width: 100%;}
    .xq img{width: 100%;height: 10rem;}
    .xq p.p1{font: .6rem/1rem '';color:#3285ff;padding-left: .3rem;font-weight: 900;width: 100%;}
    .xq p.p2{padding-left: .3rem;font-size:.5rem;font-weight: 900;width: 100%;}
    .xq .fg{width: 100%;height: .3rem;background: whitesmoke;margin-top: .3rem;}
    .xq h3{width: 100%;padding-left: .3rem;font: .4rem/1rem '';border-bottom: .2rem solid whitesmoke;}
    .xq ul{display: flex;justify-content: space-around;width: 100%;}
    .xq ul li{width: 24%;text-align: center;}
    .xq ul li img{height: 1.8rem;}
    .xq ul li p.p4{font-size: .35rem;color: #3285ff;}
    .xq p.p5{font: .35rem/2rem '';background:whitesmoke;color: darkgray;}
</style>


<script>

import Headerx from '../components/Headerx'

export default {
    // name:'home',
    data(){
        return{
            detail:[],
            details:{}
        }
    },
    components:{Headerx},
    mounted(){
        // console.log(this.$route)
        let id = this.$route.params._id;
        let dataName = this.$route.query.dataName;
        // console.log(id,dataName)
        function getUserAccount() {
            return axios.get(`/api/${dataName}`);
            }
            
        function getUserPermissions() {
            return axios.get(`/api/${dataName}/${id}`);
            }
 
        axios.all([getUserAccount(), getUserPermissions()])
        .then(
            acct=>{
                this.details = acct[1].data.data
                this.detail = acct[0].data.data
            })
    }
}    
</script>
