<template>
    <div class="follow">
        <Headerc></Headerc>
        <ul>
            <li v-for='(item,index) of list' :key='item._id'>
                <router-link :to="`/detailsf/${item._id}?dataName=follow`">
                    <img :src='item.src'/>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<style scoped>
    .follow ul{display: flex;justify-content: space-around;flex-wrap: wrap;width: 100%;margin: .1rem 0 1.3rem 0;}
    .follow ul li img{width: 4.8rem;height: 6rem;}
</style>

<script>

import Headerc from '../components/Headerc'

export default {
    name:'follow',
    data(){
        return{
            list:[]
        }
    },
    components:{Headerc},
    mounted(){
        axios({
            url:'/api/follow',
        }).then(
            res=>this.list = res.data.data
        )
    }
}
</script>
