import Vue from 'vue'
import Vuerouter from 'vue-router'
Vue.use(Vuerouter)

import Home from '../pages/Home'
import Follow from '../pages/Follow'
import Column from '../pages/Column'
import User from '../pages/User'
import Login from '../pages/Login'
import Reg from '../pages/Reg'
import Details from '../components/Details'
import Detailsf from '../components/Detailsf'
import Detailsff from '../components/Detailsff'


let routes = [{
    path:'/home',
    component:Home
},{
    path:'/follow',
    component:Follow
},{
    path:'/column',
    component:Column
},{
    path:'/user',
    component:User
},{
    path:'/details/:_id',
    component:Details
},{
    path:'/detailsf/:_id',
    component:Detailsf
},{
    path:'/detailsff/:_id',
    component:Detailsff
},{
    path:'/login',
    component:Login
},{
    path:'/reg',
    component:Reg
},{
    path:'/',
    redirect:'/home'}]

let router = new Vuerouter({
    routes:routes
})

export default router