<template>
    <div class="list">
        <ul>
            <li v-for='(item,index) of list' :key='item._id'>
                <router-link :to="`/details/${item._id}?dataName=${dataName}`">
                    <img :src="item.src" class="img1"/>
                    <P class="p1">{{item.name}}</P>
                    <P class="p2">{{item.price}}</P>
                </router-link>
                <van-button  @click="showPopup" type='default' >
                    <img src='../assets/img/car3.jpg' class="img2" />
                </van-button>
                <van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
                    <img :src="item.src" class="img3"/>
                    <span>{{item.price*$store.state.count}}</span>
                    <b></b>
                    <button @click='add(0)'>+</button>
                        {{$store.state.count}}
                    <button @click='rec(0)'>-</button>
                    <p class="p3"></p>
                    <p class="p4"></p>
                </van-popup>
            </li>
        </ul>
    </div>
</template>

<script>

import Vue from 'vue';

import { Popup,Button } from 'vant';

import {mapActions,mapGetters} from 'vuex'

Vue.use(Popup);
Vue.use(Button);

export default {
    props:['list','dataName'],
    data(){
        return {
            show: false
        }
    },
    methods:{
        showPopup(){
            this.show = true
        },
        add(){
            this.$store.dispatch('add')
        },
        rec(){
            this.$store.dispatch('rec')
        }
    }
}
</script>

<style scoped>
    .list ul{display: flex;justify-content: space-around;flex-wrap: wrap;}
    .list ul li img.img1{width: 4rem;height: 3rem;display: block;}
    .list ul li img.img2{width: .6rem;height: .6rem;border: 0;}
    .list ul li img.img3{width: 4rem;height: 3rem;border: 0;margin-left: -3.8rem;}
    .list ul li p.p1{font: .4rem/1rem '';display: inline-block;}
    .list ul li p.p2{font: .3rem/.6rem '';color:cornflowerblue;}
    .list ul li span{display: inline-block;font:.4rem/1rem '';margin-top: -2rem;color: cornflowerblue;}
    .list ul li b{display: block;margin:0rem 0 0 0rem;width: 25%;}
    .list ul li p.p3{width: 50%;font: .4rem/1rem '';color: #fff;background: blue;float: left;margin-top: .58rem;}
    .list ul li p.p4{width: 50%;font: .4rem/1rem '';color: #fff;background: cornflowerblue;float: right;}
</style>
