<template>
    <div class="login">
        <h3></h3>
        <img src="../assets/img/wx.jpg"/>
        <span><input type="text" value="" class="ipt1" v-model="username"/></span>
        <span><input type="text" value="" class="ipt1" v-model="password"/></span>
        <input type="button" value="" class="ipt2" @click="login"/><br/>
        <a href="javascript:;">
            <router-link to='/reg'>
                
            </router-link>
        </a>
    </div>
</template>

<style scoped>
    .login h3{font: .5rem/1.3rem '';width: 100%;border-bottom:.1rem solid whitesmoke;text-align: center;}
    .login img{width: 3rem;height: 3rem;border-radius: 50%;border: .05rem solid black;margin:1rem 0 1rem 3.5rem;overflow: hidden;}
    .login span{display: block;font:.4rem/1rem '';padding-left: 1rem;border-bottom:.1rem solid whitesmoke;color:#333333;}
    .login span input.ipt1{color:#d6dada;}
    .login input.ipt2{font: .4rem/1rem '';padding:0 1.5rem;background:#3285ff;
    color: honeydew;border-radius: .2rem;margin:.8rem 0 0 3rem;}
    .login a{display: inline-block;font: .35rem/.5rem '';margin:.3rem 0 0 2.15rem;}
</style>

<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      mess:''
    }
  },
  methods:{
    login(){
      axios({
        url:'/api/login',
        method:'post',
        data:{username:this.username,password:this.password}
      }).then(
        res => {
          // console.log('1',res);
          if(res.data.err===0){
            this.$router.push('/user')
          }else{
            this.mess = res.data.msg
          }
        }
      )
    }
  }
}
</script>
